.product-image-lists {


  .carousel {
    .control-arrow {
      display: none;
      top: 57%;
      bottom: auto;
      height: 30px;
      width: 30px;
      transform: translateY(-25px);
      padding: 0 2px;
      border: 2px solid white;
      opacity: 0.8;
      border-radius: 50%;
      box-shadow: 0px 0px 0px 1px transparent, 0px 0px 0px 4px transparent,
        0px 2px 4px rgba(0, 0, 0, 0.18) !important;

      &:hover {
        opacity: 1;
      }

      &.control-next,
      &.control-prev {
        background: white;

        &:before {
          content: ' ';
          margin: 0;
          display: inline-block;
          width: 10px;
          height: 10px;
          border: 2px solid;
          border-bottom: 0;
          border-left: 0;
          text-indent: -624.9375rem;
        }
      }

      &.control-next {
        margin-right: 10px;

        &:before {
          margin-right: 13px;
          margin-left: 10px;
          -webkit-transform: rotate(46deg);
          -moz-transform: rotate(46deg);
          -ms-transform: rotate(46deg);
          -o-transform: rotate(46deg);
          transform: rotate(46deg);
          color: black;
        }
      }

      &.control-prev {
        margin-left: 10px;

        &:before {
          margin-left: 15px;
          margin-right: 13px;
          -webkit-transform: rotate(225deg);
          -moz-transform: rotate(225deg);
          -ms-transform: rotate(225deg);
          -o-transform: rotate(225deg);
          transform: rotate(225deg);
          color: black;
        }
      }
    }

    & .control-dots {
      display: flex;
      max-width: 60px;
      overflow: hidden;
      flex-direction: row;
      right: 40%;

      .dot {
        margin: 0 3px;
        flex: none;
        width: 10px;
        height: 10px;
        outline: none;
      }
    }
  }
}

.product-item {
  &:hover .control-arrow {
    display: flex !important;
  }
}