  .lease {
      max-width: 400px;
      width: 90vw;
  }

  .pdf-doc {
      max-width: 400px;
      margin-bottom: 16px;
      width: 90vw;
      border-radius: 8px;
      position: relative;
      box-shadow: 0 30px 40px 0 rgba(16, 36, 94, 0.2);
  }

  .page-controls {
      position: absolute;
      display: flex;
      bottom: 5%;
      left: 50%;
      background: white;
      opacity: 0;
      box-shadow: 0 30px 40px 0 rgba(16, 36, 94, 0.2);
      border-radius: 4px;
  }

  .pdf-doc:hover .page-controls {
      opacity: 1;
  }

  .page-controls span {
      font: inherit;
      font-size: 0.8em;
      padding: 0 0.5em;
      line-height: 45px;
  }

  .page-controls button:last-child {
      border-top-left-radius: 0;
      border-bottom-left-radius: 0;
  }

  .page-controls button {
      width: 44px;
      height: 44px;
      background: white;
      border: 0;
      font: inherit;
      font-size: 0.8em;
      border-radius: 4px;
  }

  .page-controls button:first-child {
      border-top-right-radius: 0;
      border-bottom-right-radius: 0;
  }

  .react-pdf__Page__textContent {
      display: none !important;
  }

  .react-pdf__Page__canvas {
      max-width: 400px !important;
      width: 90vw !important;
      height: 100% !important;
      border-radius: 8px;
  }

  .react-pdf__Page__annotations {
      display: none !important;
  }

  .lease-docs {
      display: flex;
      justify-content: space-evenly;
      flex-wrap: wrap;
  }

  .action-lease-btn {
      margin: 15px;
      bottom: 0;
      position: absolute;
      right: 0;
  }