#tab_item_container {
    justify-content: center;
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 70px;
    border-width: 0px;

    .icon_div_style {
        width: 100%;
        justify-content: center;
        align-items: center;
        display: flex;
        height: 40px;
        border-width: 0px;
    }
    
    .icon_text_style {
        display: flex;
        width: 100%;
        justify-content: center;
        font-size: 14px;
        height: 30px;
        border-width: 0px;
    }
    
    .tab_search_icon {
        color: #747474;
        width: 24px !important;
        height: 24px;
        margin-top: 8px;
    }
    
    .tab_text {
        color: #747474;
        font-size: 14px;
    }
    
    .select_style {
        color: black;
    }
    
    .unselected_style {
        color: #747474;
    }
}


#tab_item_container {
    @media (max-width: 760px) {
        height: 60px;
    
        .icon_div_style{
            height: 35px;
        }
    
        .icon_text_style{
            height: 25px;
        }
        
        .tab_search_icon {
            width: 20px !important;
            height: 20px;
            margin-top: 6px;
        }
    
        .tab_text {
            font-size: 12px;
        }
    }
}


#tab_item_container { 
    @media (max-width: 320px) {
        height: 50px;
    
        .icon_div_style{
            height: 30px;
        }
    
        .icon_text_style{
            height: 20px;
        }
        
        .tab_search_icon {
            width: 18px !important;
            height: 18px;
            margin-top: 4px;
        }
    
        .tab_text {
            font-size: 10px;
        }
    }
}
