@keyframes verificationBox {
  from {right: -300px}
  to {right: 0;}
}

.verificationBox {
  background: #42b860;
  color: white;
  border: solid black;
  border-radius: 10px;
  position: fixed;
  z-index:999;
  padding: 0px 10px 10px 10px;
  margin:10px;
  right: 0;
  border-width: 1px;
  animation-name: verificationBox;
  animation-duration: 500ms;
}

.react-tabs__tab-list {
  margin: revert!important;
}

@media (max-width: 400px) {
  .verificationBox {
    margin-left: 5px;
    margin-right: 5px;
  }
}