.s003 {
    background: #fff;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -ms-flex-align: center;
    align-items: center;
    box-shadow: 0px 8px 20px 0px rgba(0, 0, 0, 0.15);
    border-radius: 3px;
  }
  
  .s003 .input-field {
    height: 48px;
  }
  
  .s003 .input-field input {
    height: 100%;
    background: transparent;
    border: 0;
    display: block;
    width: 100%;
    padding: 10px 32px;
    font-size: 16px;
    color: #555;
  }
  
  .s003 .input-field input.placeholder {
    color: #888;
    font-size: 16px;
  }
  
  .s003 .input-field input:-moz-placeholder {
    color: #888;
    font-size: 16px;
  }
  
  .s003 .input-field input::-webkit-input-placeholder {
    color: #888;
    font-size: 16px;
  }
  
  .s003 .input-field input:hover,
  .s003 .input-field input:focus {
    box-shadow: none;
    outline: 0;
    border-color: #fff;
  }
  
  .s003 .input-field.first-wrap {
    width: 200px;
    border-right: 1px solid rgba(0, 0, 0, 0.1);
  }
  
  .s003 .input-field.first-wrap .choices__inner {
    background: transparent;
    border-radius: 0;
    border: 0;
    height: 100%;
    color: #fff;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
    padding: 10px 30px;
  }
  
  .s003
    .input-field.first-wrap
    .choices__inner
    .choices__list.choices__list--single {
    display: -ms-flexbox;
    display: flex;
    padding: 0;
    -ms-flex-align: center;
    align-items: center;
    height: 100%;
  }
  
  .s003
    .input-field.first-wrap
    .choices__inner
    .choices__item.choices__item--selectable.choices__placeholder {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
    height: 100%;
    opacity: 1;
    color: #888;
  }
  
  .s003
    .input-field.first-wrap
    .choices__inner
    .choices__list--single
    .choices__item {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
    height: 100%;
    color: #555;
  }
  
  .s003 .input-field.first-wrap .choices[data-type*='select-one']:after {
    right: 30px;
    border-color: #e5e5e5 transparent transparent transparent;
  }
  
  .s003 .input-field.first-wrap .choices__list.choices__list--dropdown {
    border: 0;
    background: #fff;
    padding: 20px 30px;
    margin-top: 2px;
    border-radius: 4px;
    box-shadow: 0px 8px 20px 0px rgba(0, 0, 0, 0.15);
  }
  
  .s003
    .input-field.first-wrap
    .choices__list.choices__list--dropdown
    .choices__item--selectable {
    padding-right: 0;
  }
  
  .s003
    .input-field.first-wrap
    .choices__list--dropdown
    .choices__item--selectable.is-highlighted {
    background: #fff;
    color: silver;
  }
  
  .s003 .input-field.first-wrap .choices__list--dropdown .choices__item {
    color: #555;
    min-height: 24px;
  }
  
  .s003 .input-field.second-wrap {
    -ms-flex-positive: 1;
    flex-grow: 1;
  }
  
  .s003 .input-field.third-wrap {
    width: 20px;
    margin: 0px 32px -28px 0px;
  }
  
  .third-wrap svg:hover {
    height: 22px;
    cursor: pointer;
  }
  
  .s003 .input-field.third-wrap .btn-search {
    height: 100%;
    width: 100%;
    white-space: nowrap;
    color: #fff;
    border: 0;
    cursor: pointer;
    background: black;
  }
  
  .s003 .input-field.third-wrap .btn-search svg {
    width: 16px;
  }
  
  .s003 .input-field.third-wrap .btn-search:hover {
    background: #53c75b;
  }
  
  .s003 .input-field.third-wrap .btn-search:focus {
    outline: 0;
    box-shadow: none;
  }
  
  /* custom select */
  .custom-select {
    position: relative;
    font-family: Arial;
  }
  .select-selected:hover {
    background: rgb(19, 19, 19);
  }
  .custom-select select {
    display: none; /*hide original SELECT element:*/
  }
  
  .select-selected {
    background-color: black;
  }
  
  /*style the arrow inside the select element:*/
  .select-selected:after {
    position: absolute;
    content: '';
    top: 21px;
    right: 10px;
    width: 0;
    height: 0;
    border: 6px solid transparent;
    border-color: #fff transparent transparent transparent;
  }
  
  /*point the arrow upwards when the select box is open (active):*/
  .select-selected.select-arrow-active:after {
    border-color: transparent transparent #fff transparent;
    top: 14px;
  }
  
  /*style the items (options), including the selected item:*/
  .select-items div,
  .select-selected {
    color: #ffffff;
    padding: 8px 16px;
    border: 1px solid transparent;
    border-color: transparent transparent rgba(0, 0, 0, 0.1) transparent;
    cursor: pointer;
    user-select: none;
  }
  
  /*style items (options):*/
  .select-items {
    position: absolute;
    background-color: black;
    top: 100%;
    left: 0;
    right: 0;
    z-index: 99;
  }
  
  /*hide the items when the select box is closed:*/
  .select-hide {
    display: none;
  }
  
  .select-items div:hover,
  .same-as-selected {
    background-color: #53c75b;
  }
  
  .search-component {
    width: 95%;
    max-width: 760px;
    margin: 30px auto 60px auto;
  }
  
  .searched-container {
    display: flex;
    text-overflow: unset;
    flex-direction: column;
  }
  
  .searched-product {
    text-align: center;
    border: 1px solid #b2b2b2;
    border-radius: 3px;
    margin: 5px 5px;
    padding: 5px 12px;
    text-decoration: none;
    font-size: 13px;
  }
  
  .searched-product:hover {
    background-color: black;
    color: white;
  }
  
  @media (max-width: 700px) {
    .popular-txt {
      margin-left: 5px;
    }
    .first-wrap {
      display: none;
    }
  }
  
  @media (max-width: 790px) {
    .search-component {
      width: 95%;
      max-width: 760px;
      margin: 30px auto 30px auto;
    }
  }