.avatar_img {
  max-width: 200px;
  display: flex;
  margin: 0 auto;
  border-radius: 50%;
  justify-content: center;
  border: 6px solid white;
}

#container {
  padding-top: 0px !important;
}

.edit_img {
  width: 200px;
  display: flex;
  margin: 0 auto;
  border-radius: 50%;
  justify-content: center;
  height: 200px;
  object-fit: cover;
  margin-bottom: 10px;
}

.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 1;
}

.container {
  cursor: pointer;
  position: relative;
  width: 100%;
  max-width: 400px;
}

.container:hover .edit_img {
  filter: brightness(25%);
  transition: ease-in-out 0.03s;
}

.icon {
  position: absolute;
  top: 50%;
  transform: translate(15%, -50%);
  text-align: center;
}

.uploadBackground {
  border-radius: 5px;
  width: 18rem;
  max-width: 100%;
  margin: 0 auto;
}
