#btn_container {
  &.full_width,
  .full_width {
    width: 100%;
  }

  .mobileIcon,
  button {
    outline: none;
    color: #ffffff;
    background-color: #000000;
    font-weight: bold;
    &:hover {
      background-color: white;
      color: black;
    }
  }
  .mobileIcon {
    border-radius: 4px;
    padding: 6px 10px;
  }
}
