#filter_main_container {

    .filter_container {
        display: flex;
        margin-top: 8px;
        max-width:400px;
    }

    .filter_item {
        flex:auto;
        margin: 0;
        border: 0;
        margin-right: 8px;
        display: inline-flex;
        padding: 0;
        position: relative;
        min-width: 0;
        flex-direction: column;
        vertical-align: top;
    }

    .filter_btn_item {
        margin-right: 8px;
        border: 1px solid rgba(0, 0, 0, 0.23);
        border-color: rgba(0, 0, 0, 0.1);
        background-color: transparent;
        cursor: pointer;
        -webkit-tap-highlight-color: transparent;
        color: rgba(0, 0, 0, 0.87);
        height: 32px;
        display: inline-flex;
        outline: none;
        padding: 0;
        font-size: 0.9285714285714286rem;
        transition: background-color 20ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
        align-items: center;
        white-space: nowrap;
        border-radius: 16px;
        vertical-align: middle;
        justify-content: center;
        text-decoration: none;
    }

    .filter_btn_item:last-child, .filter_item:last-child {
        margin-right: 0px;
    }

    .filter_text {
        cursor: inherit;
        display: flex;
        align-items: center;
        user-select: none;
        white-space: nowrap;
        padding-left: 12px;
        padding-right: 12px;
    }

    .align-inherit {
        vertical-align: inherit;
    }

    .filter_arrow_style {
        color: black;
        cursor: pointer;
        margin: 0 4px 0 -8px;
        -webkit-tap-highlight-color: transparent;
        fill: currentColor;
        width: 1em !important;
        height: 1em !important;
        display: inline-block;
        font-size: 24px;
        transition: fill 20ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
        user-select: none;
        flex-shrink: 0;
    }

    .drop_down_container {
        display: flex;
        flex: 1;
        align-items: center;
        margin-top: 20px;
    }

    .drop_down_item {
        flex: 0.33;
        display: flex;
        border: 2px solid rgba(12, 12, 12, 0.23);
        border-color: rgba(0, 0, 0, 0.1);
    }

    .drop_down_items_container {
        position: absolute;
        top: 50px;
        display: flex;
        left: 0;
        z-index: 100;
        right: 0;
    }

    .drop_down_items_card {
        width: 100%;
    }

    .drop_down_new_style {
        width: 100%;
        background-color: white;
        color: rgba(0, 0, 0, 0.87);
        border-color: #e5e5e5;
        box-sizing: border-box;
        border-radius: 2px;
        box-shadow: 0 0 0 0.2rem rgba(0, 0, 0, 0);
        font-family: sans-serif;
    }

    .drop_down_new_style::after {
        margin-left: .255em;
    }

    .item_right {
        right: 0;
        left: 0;
        top: 40px;
    }

    .item_right_main {
        right: 0;
        left: 0;
        top: 40px;
        margin-bottom: 0!important;
    }

    .range_style_row {
        margin-left: 0px;
        margin-right: 0px;
        display: flex;
        justify-content: space-evenly;

    }
    
    .spacing_control {
        margin: 0 5px;
    }

    .dropdown_item {
        padding: 0.25rem 1.0rem;
    }

    .dropdown_item_1 {
        padding: 0.25rem 0.25rem;
    }

    .drop_down_filter_container {
        margin-top: 10px;
    }

    .col_container_style {
        padding-right: 4px;
        padding-left: 4px;
        padding-bottom: 10px;
    }

    .input_style_color {
        border: 1px solid #f3f3f3;
        height: 40px;
    }
}

#filter_main_container {
    @media (max-width: 760px) {
        .filter_btn_item {
            margin-right: 4px;
            height: 24px;
            border-radius: 12px;
            margin-top: 8px;
        }

        .filter_text {
            font-size: 12px !important;
            padding-left: 10px;
            padding-right: 10px;
        }

        .filter_arrow_style {
            font-size: 16px;
        }
    }
}

#filter_main_container {
    @media (max-width: 320px) {
        .filter_btn_item {
            margin-right: 2px;
            height: 20px;
            border-radius: 10px;
            margin-top: 4px;
        }

        .filter_text {
            font-size: 10px !important;
            padding-left: 8px;
            padding-right: 8px;
        }

        .filter_arrow_style {
            font-size: 14px !important;
        }
    }
}