#ad_rating_container {
    display: flex;
    flex-wrap: wrap;
    box-sizing: border-box;
    align-items: flex-end;
    flex: 1;
    height: 40px;
    align-items: center;

    .reset_button {
        background-color: white!important;
        color:black!important;
        border-color: white!important;
        box-shadow: none!important;
        padding-left: 0;
    }
    .ad_rating_items {
        color: rgba(0, 0, 0, 0.54);
        display: flex;
        flex-wrap: wrap;
        flex-direction: row;
        margin-left:1.07rem;
    }

    .ad_rating_item_large {
        margin-right: 4px;
        display: block;
    }

    .ad_rating_item_small {
        margin-right: 4px;
        display: none;
    }

    .ad_rating_item2 {
        cursor: pointer;
    }

    .ad_rating_item2_content {
        color: rgba(0, 0, 0, 0.87);
        display: flex;
        align-items: center;
    }

    .ad_rating_item2_arrow {
        fill: currentColor;
        width: 1em;
        height: 1em;
        display: inline-block;
        font-size: 24px;
        transition: fill 20ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
        -webkit-user-select: none;
        user-select: none;
        flex-shrink: 0;
        margin-left: 4px;
    }
}

#ad_rating_container { 
    @media (max-width: 760px) {
        .ad_rating_item2_arrow  {
            font-size: 14px;
        }
    
        .ad_rating_container {
            font-size: 14px;
        }
    
        .ad_rating_item_large {
            display: none;
        }
        
        .ad_rating_item_small {
            display: block;
        }
    }
}

#ad_rating_container { 
    @media (max-width: 320px) { 
        .ad_rating_item2_arrow  {
            font-size: 12px;
        }

        .ad_rating_container {
            font-size: 12px;
        }

        .ad_rating_item_small {
            display: block;
            font-size: 9px;
        }

        .ad_rating_item2_content {
            font-size: 9px;
        }
    }
}