
.centered {
    font-size: 3vw;
    color: white;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: black;
    padding-left: 1.5%;
    padding-right: 1.5%;
    border-radius: 15px;
    width: 31%;
    height: 49%;
    letter-spacing: 0.2em;
      line-height: 1.35;
  }
  .bottom-right {
    font-size: 2.8vw;
    color: white;
    position: absolute;
    top: 0px;
    bottom: 0px;
    right: 0px;
    background-color: black;
    padding-left: 1.5%;
    padding-right: 1.5%;
    width: 34%;
    letter-spacing: 0.2em;
      line-height: 1.35;
  }
  .bottom-right p{
    font-size: 1.5vw;
    letter-spacing: 0em;
      line-height: 1.2;
  }
  
  .vertical-center {
    margin: 0;
    position: absolute;
    top: 50%;
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
  }
  
  @media screen and (max-width: 560px) {
    .centered{
      font-size: 18px;
      width: 186px;
    }
    .bottom-right{
      font-size: 19px;
      width: 203px;
    }
    .bottom-right p{
      font-size: 10px;
    }
  ;
  }