#image_container {
    width: 100%;
    height: 100%;
    position: relative;

    .image_content {
        width: 100%;
        height: 100%;
        opacity: 1;
        transition: opacity 20ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    }
    
    .bit_olx_image{
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
    }
}

