
#product_component {
    .main_product {
        min-height: 80vh;
    }
    
    section {
        margin-top: 4px;
    }
    
    .search_container {
        margin: 20px;
        margin-bottom: 24px;
    }
    
    .search_item{
        display: flex;
        flex:1;
    }
    
    .search_btn_style {
        display: block !important;
        border-radius: 3px;
    }
}

#product_component {
    @media (max-width: 760px) {
        .search_btn_style {
            display: none !important;
            font-size: 14px !important;
        }
    
        .search_container {
            margin: 20px 10px;
        }
    }    
}

#product_component {
    @media (max-width: 320px) { 
        .search_container {
            margin: 12px 10px;
        }
    }
}
