#user-profile {
  .profile-card {
    padding-top: 40px;
    max-height: 400px;
  }

  .background-img {
    width: 100%;
    height: 250px;
    object-fit: cover;
    border-radius: 0 0 10px 10px;
  }
  .avatar {
    position: relative;
    left: 0;
    right: 0;
    z-index: 2;
    bottom: 160px;
    text-align: center;
    img {
      width: 170px;
      height: 170px;
      object-fit: cover;
      display: unset;
    }
  }

  .user-name {
    color: black;
    font-weight: 400;
    line-height: 1.46429em;
    max-width: 100%;
    margin: 0;
  }

  .btn-action {
    display: flex;
    position: absolute;
    right: 29px;
    top: 287px;
    z-index: 3;
  }

