#icon_container {
  outline: none;
  color: #ffffff;
  background-color: #000000;
  font-weight: bold;
  border-radius: 4px;
  padding: 6px 10px;
  box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
    0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
  &:hover {
    background-color: white;
    color: black;
  }
}
