.center-wrapper {
    width: 1200px;
    margin: 0 auto 40px auto;
  }
  
  .section-links-wrapper {
    display: -ms-inline-flexbox;
    display: inline-flex;
    width: 100%;
    -ms-flex-pack: justify;
    height: 378px;
  }
  
  .text-black {
    color: black;
    background: white;
    border-radius: 4px;
    padding: 0 4px;
  }
  
  .section-link:hover {
    text-decoration: none;
  }
  
  .section-link {
    -webkit-box-shadow: 0 2px 10px 0 rgba(39, 39, 43, 0.5);
    box-shadow: 0 2px 10px 0 rgba(39, 39, 43, 0.5);
  }
  
  a:hover {
    color: #1a1a1a;
    text-decoration: underline;
  }
  
  .section-link {
    border-radius: 3px;
    position: relative;
    height: 180px;
    overflow: hidden;
    background-color: #333;
    z-index: 1;
  }
  
  .section-link:hover:before {
    filter: brightness(107%);
  }
  
  .section-link--business:before {
    background-image: url(img/business.jpg);
    background-repeat: no-repeat;
  }
  
  .section-link:before {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background-size: cover;
    background-position: center;
  }
  
  .section-link__title {
    font-size: 24px;
    font-weight: 700;
    letter-spacing: -0.5px;
    color: #fff;
    display: block;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    padding: 22px 32px;
    position: relative;
    z-index: 2;
  }
  
  .section-links-inner-wrapper {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
    flex-direction: row;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -ms-flex-line-pack: justify;
    align-content: space-between;
    -ms-flex-pack: justify;
    justify-content: space-between;
  }
  
  .section-link {
    border-radius: 3px;
    position: relative;
    height: 180px;
    overflow: hidden;
    background-color: #333;
    z-index: 1;
  }
  
  a {
    color: #333;
    text-decoration: none;
    cursor: pointer;
  }
  
  .section-link--flat_rent:before {
    background-image: url(img/flat-rent.jpg);
    background-position-x: 32%;
    background-position-y: 94%;
    background-size: auto 142%;
    background-repeat: no-repeat;
  }
  
  .section-link--franchise:before {
    background-image: url(img/franchise.jpg);
    background-position-x: 6%;
    background-position-y: 40%;
    background-size: auto 192%;
    background-repeat: no-repeat;
  }
  
  .section-link--construction:before {
    background-image: url(img/construction.jpg);
    background-position-x: 6%;
    background-position-y: 40%;
    background-size: auto 130%;
    background-repeat: no-repeat;
  }
  
  .section-link--apartments:before {
    background-image: url(img/apartments.jpg);
    background-position-x: 40%;
    background-position-y: 24%;
    background-size: auto 147%;
    background-repeat: no-repeat;
  }
  
  @media only screen and (min-width: 791px) {
    .section-link--business {
      margin-right: 18px;
      width: 388px;
      height: 100%;
    }
  
    .section-links-inner-wrapper {
      max-width: 794px;
    }
  
    .section-link {
      width: 100%;
      max-width: 389px;
    }
  }
  
  @media only screen and (max-width: 790px) {
    .center-wrapper {
      width: unset;
      margin: 0;
      margin-bottom:20px;
    }
  
    .section-links-wrapper {
      margin-left: auto;
      margin-right: auto;
    }
  
    /* next */
    .section-link {
      height: 154px;
      border-radius: 0;
    }
  
    .main-container {
      padding-top: 2px;
    }
  
    .section-links-wrapper {
      display: -ms-flexbox;
      display: flex;
      -ms-flex-direction: row;
      flex-direction: row;
      -ms-flex-wrap: wrap;
      flex-wrap: wrap;
      height: auto;
    }
  
    .section-link {
      height: 154px;
      width: 100%;
    }
  
    .section-link__title {
      font-size: 18px;
      line-height: 1.33;
      letter-spacing: -0.3px;
      padding: 14px 22px;
    }
  
    .section-links-inner-wrapper {
      display: none;
    }
  }
  
  @media only screen and (min-width: 791px) and (max-width: 1243.95px) {
    .center-wrapper {
      width: 790px;
    }
  
    .section-link--business {
      margin-right: 18px;
      margin-left: 18px;
      width: 388px;
      height: 100%;
    }
  
    .section-links-inner-wrapper {
      max-width: 498px;
    }
  
    .section-link {
      width: 100%;
      max-width: 240px;
    }
  }