#drop_down_item_container {
    display: flex;

    .icon_btn_container {
        border-color: white !important;
        background-color: white !important;
        color: rgba(0, 0, 0, 0.87) !important;
        padding: 8px 16px;
        font-size: 1rem;
        cursor: pointer;
        min-width: 64px;
        box-sizing: border-box;
        min-height: 36px;
        transition: background-color 20ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,box-shadow 20ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,border 20ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
        font-family: Roboto, sans-serif;
        font-weight: 500;
        line-height: 1.5;
        border-radius: 2px;
        text-transform: uppercase;
        border: 0;
        margin: 0;
        display: inline-flex;
        outline: none;
        position: relative;
        align-items: center;
        user-select: none;
        vertical-align: middle;
        justify-content: center;
        text-decoration: none;
        background-color: transparent;
        -webkit-appearance: none;
        -webkit-tap-highlight-color: transparent;
    }
    
    .icon_btn_item {
        width: 100%;
        display: inherit;
        align-items: inherit;
        justify-content: inherit;
    }
    
    .btn_icon_style {
        margin-right: 4px;
        fill: currentColor;
        width: 1em;
        height: 1em;
        display: inline-block;
        font-size: 24px;
        transition: fill 20ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
        user-select: none;
        flex-shrink: 0;
        color: rgba(0, 0, 0, 0.54);
    }
}