#drop_down_list_container{
    .dropbtn {
        background-color: #4CAF50;
        color: white;
        padding: 16px;
        font-size: 16px;
        border: none;
        cursor: pointer;
      }
      
      .dropdown_container {
        position: relative;
        display: inline-block;
      }
      
      .dropdown_content {
        display: none;
        position: absolute;
        background-color: #f9f9f9;
        min-width: 160px;
        box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
        z-index: 100;
      }
      
      .dropdown_content a {
        color: black;
        padding: 12px 16px;
        text-decoration: none;
        display: block;
      }
      
      .dropdown_content a:hover {background-color: #f1f1f1}
      
      .dropdown_container:hover .dropdown_content {
        display: block;
      }
      
      .dropdown_container:hover .dropbtn {
        background-color: #3e8e41;
      }

      .filter_arrow_style {
        font-size: 16px;
        }
}