#search_box {
    flex: 1;
    justify-content: center;
    align-items: center;
    height: 48px;

    .search_background {
        background-color: #fff;
    }
    .search_border {
        border-radius: 2px;
    }
    .search_box_shadow  {
        box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.1);
    }
    
    .search-item {
        display: flex;
        position: relative;
        flex: 1;
        align-items: center;
    }
    
    .search_box_container {
        height: 100%;
        cursor: text;
        border: 1px solid;
        padding: 0px 10px;
        align-items: center !important;
        display: flex;
        outline: none;
        flex-wrap: wrap;
        min-height: 40px;
        box-shadow: none;
        align-items: flex-start;
        border-color: rgba(0, 0, 0, 0.1);
        padding-bottom: 0;
    }
    
    
    .search_icon {
        height: 32px;
        padding-right: 8px;
        fill: currentColor;
        width: 1em;
        display: inline-block;
        font-size: 24px;
        transition: fill 20ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
        user-select: none;
        flex-shrink: 0;
    }
    
    .search_label_container {
        color: rgba(0, 0, 0, 0.54);
        height: 32px;
        line-height: 32px;
    }
    
    .search_align {
        vertical-align: inherit;
    }
}


#search_box {
    @media (max-width: 760px) {
        height: 40px;
        .search_box_container {
           min-height: 30px !important;
        }
        
        .search_icon { 
            height: 24px !important;
            font-size: 20px !important;
        }
        
        .search_label_container {
            height: 24px !important;
            line-height: 24px  !important;
            font-size: 14px;
        }   
    }
}

    
