#bit_olx_drop_down {
    .icon_btn_container {
        border-color: white !important;
        background-color: white !important;
        color: rgba(0, 0, 0, 0.87) !important;
        padding: 8px 16px;
        font-size: 1rem;
        cursor: pointer;
        min-width: 64px;
        box-sizing: border-box;
        min-height: 36px;
        transition: background-color 20ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,box-shadow 20ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,border 20ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
        font-family: Roboto, sans-serif;
        font-weight: 500;
        line-height: 1.5;
        border-radius: 2px;
        text-transform: uppercase;
        border: 0;
        margin: 0;
        display: inline-flex;
        outline: none;
        position: relative;
        align-items: center;
        user-select: none;
        vertical-align: middle;
        justify-content: center;
        text-decoration: none;
        background-color: transparent;
        -webkit-appearance: none;
        -webkit-tap-highlight-color: transparent;
    }
    
    .icon_btn_container:focus {
        box-shadow: 0 0 0 0.2rem white !important;
    }
    
    .icon_btn_item {
        width: 100%;
        display: inherit;
        align-items: inherit;
        justify-content: inherit;
    }
    
    .btn_icon_style {
        margin-right: 4px;
        fill: currentColor;
        width: 1em;
        height: 1em;
        display: inline-block;
        font-size: 24px;
        transition: fill 20ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
        user-select: none;
        flex-shrink: 0;
        color: rgba(0, 0, 0, 0.54);
    }
    
    .dropdown_toggle::after {
        display: none !important;
    }
    
    .dropdown-menu {
        border: 0px solid rgba(0,0,0,.15) !important;
    }
    
    .dropdown_card {
        /* Add shadows to create the "card" effect */
        box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
        transition: 0.03s;
      }
      
      /* On mouse-over, add a deeper shadow */
    .dropdown_card:hover {
        box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
    }
}

#bit_olx_drop_down {
    @media (max-width: 760px) {
        .icon_btn_container {
            padding: 5px 10px;
            min-height: 30px;
            font-weight: 400;
            font-size: 0.8rem;
        }
    
        .btn_icon_style {
            font-size: 18px;
        }
    }
    
}

#bit_olx_drop_down {
    @media (max-width: 320px) {
        .icon_btn_container {
            padding: 5px 8px;
            min-height: 30px;
            font-weight: 400;
            font-size: 0.6rem;
        }

        .btn_icon_style {
            font-size: 14px;
        }
    }
}